import Vue from 'vue'
import Router from 'vue-router'
//import Login from "../views/Login"
import main from '../views/main'
import mainTwo from "../views/mainTwo";
//import roomManage from "../views/main/roomManage";
//import structureManage from "../views/main/structureManage";

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      // 登录页
      path: '/',
      name: 'login',
      component:  () => import(/* webpackChunkName: "about" */ '../views/login')
    },
    {
      // 首页
      path: '/main',
      name: 'main',
      component: main,
      meta: {
        requireAuth: true
      },
      children: [
        {
          path: '',
          name: 'welcome',
          component: () => import('../views/welcome/welcome'),
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/main/stuctureManage',
          name: 'stuctureManage',
          component: resolve => require(['@/views/main/structureManage/structureManage'],resolve),
          meta: {
            requireAuth: true
          }
          //component: () => import('../views/main/structureManage/structureManage')
        },
        {
          path:'/main/roomManage',
          name:'roomManage',
          component: () => import('../views/main/roomManage/roomManage'),
          meta: {
            requireAuth: true
          }
        },
        {
          path:'/main/customerManage',
          name:'customerManage',
          component: resolve =>require(['@/views/main/customerManage/customerManage'],resolve),
          meta: {
            requireAuth: true
          }
        },
        {
          path:'/main/carportManage',
          name:'carportManage',
          component: resolve =>require(['@/views/main/carportManage/carportManage'],resolve),
          meta: {
            requireAuth: true
          }
        },
        {
          path:'/main/deviceManage',
          name:'deviceManage',
          component: resolve =>require(['@/views/main/deviceManage/deviceManage'],resolve),
          meta: {
            requireAuth: true
          }
        },
        {
          path:'/main/repairManage',
          name:'repairManage',
          component: resolve =>require(['@/views/main/repairManage/repairManage'],resolve),
          meta: {
            requireAuth: true
          }
        },
        {
          path:'/main/cleanManage',
          name:'cleanManage',
          component: resolve =>require(['@/views/main/cleanManage/cleanManage'],resolve),
          meta: {
            requireAuth: true
          }
        },
        {
          path:'/main/chargeManage',
          name:'chargeManage',
          component: resolve =>require(['@/views/main/chargeManage/chargeManage'],resolve),
          meta: {
            requireAuth: true
          }
        },
        {
          path:'/main/userManage',
          name:'userManage',
          component:() => import('../views/main/userManage/userManage'),
          meta: {
            requireAuth: true
          }
        },
        {
          path:'/main/editPage',
          name:'editMessage',
          component:()=>import('../views/main/editMessage'),
          meta: {
            requireAuth: true
          }
        }
      ]
    },
    {
      path: '/mainTwo',
      name: 'mainTwo',
      component:() => import('../views/mainTwo'),
      meta: {
        requireAuth: true
      },
      children:[
        {
          path:'/mainTwo/editPage',
          name:'editPage',
          component: () => import('../views/mainTwo/editPage'),
          meta: {
            requireAuth: true
          }
        },
        {
          path: '',
          name: 'welcome',
          component: () => import('../views/welcome/welcome'),
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/mainTwo/stuctureManage',
          name: 'stuctureManageTwo',
          component: resolve => require(['@/views/mainTwo/structureManage/structureManageTwo'],resolve),
          meta: {
            requireAuth: true
          }
          //component: () => import('../views/main/structureManage/structureManage')
        },
        {
          path:'/mainTwo/roomManage',
          name:'roomManageTwo',
          component: () => import('../views/mainTwo/roomManage/roomManageTwo'),
          meta: {
            requireAuth: true
          }
        },
        {
          path:'/mainTwo/customerManage',
          name:'customerManageTwo',
          component: resolve =>require(['@/views/mainTwo/customerManage/customerManageTwo'],resolve),
          meta: {
            requireAuth: true
          }
        },
        {
          path:'/mainTwo/carportManage',
          name:'carportManageTwo',
          component: resolve =>require(['@/views/mainTwo/carportManage/carportManageTwo'],resolve),
          meta: {
            requireAuth: true
          }
        },
        {
          path:'/mainTwo/deviceManage',
          name:'deviceManageTwo',
          component: resolve =>require(['@/views/mainTwo/deviceManage/deviceManageTwo'],resolve),
          meta: {
            requireAuth: true
          }
        },
        {
          path:'/mainTwo/repairManage',
          name:'repairManageTwo',
          component: resolve =>require(['@/views/mainTwo/repairManage/repairManageTwo'],resolve),
          meta: {
            requireAuth: true
          }
        },
        {
          path:'/mainTwo/cleanManage',
          name:'cleanManageTwo',
          component: resolve =>require(['@/views/mainTwo/cleanManage/cleanManageTwo'],resolve),
          meta: {
            requireAuth: true
          }
        },
        {
          path:'/mainTwo/chargeManage',
          name:'chargeManageTwo',
          component: resolve =>require(['@/views/mainTwo/chargeManage/chargeManageTwo'],resolve),
          meta: {
            requireAuth: true
          }
        }
      ]
    }
  ]
});



