<template>
  <el-container class="el-container">
    <el-header>
      <div>
        小区物业管理系统
      </div>

      <el-button type="info" @click="dialogVisible = true">退出</el-button>

    </el-header>


    <el-container class="el-container">
      <el-aside width="200px">
        <el-menu :default-openeds="['1', '2','3','4']" >
          <el-submenu index="1">
            <template slot="title"><i class="el-icon-menu"></i>房产信息管理</template>
            <el-menu-item index="1-1"> <router-link class="router" to="/mainTwo/roomManage">房屋管理</router-link></el-menu-item>
            <el-menu-item index="1-2"><router-link class="router" to="/mainTwo/stuctureManage">楼栋管理</router-link></el-menu-item>
          </el-submenu>
          <el-submenu index="2">
            <template slot="title"><i class="el-icon-menu"></i>住户信息管理</template>
            <el-menu-item index="2-1"> <router-link class="router" to="/mainTwo/customerManage">住户管理</router-link></el-menu-item>
            <el-menu-item index="2-2"><router-link class="router" to="/mainTwo/carportManage">车位管理</router-link></el-menu-item>
          </el-submenu>
          <el-submenu index="3">
            <template slot="title"><i class="el-icon-menu"></i>物业信息管理</template>
            <el-menu-item index="3-1"><router-link class="router" to="/mainTwo/deviceManage">设备管理</router-link></el-menu-item>
            <el-menu-item index="3-2"><router-link class="router" to="/mainTwo/repairManage">维修管理</router-link></el-menu-item>
            <el-menu-item index="3-3"><router-link class="router" to="/mainTwo/cleanManage">清洁管理</router-link></el-menu-item>
            <el-menu-item index="3-4"><router-link class="router" to="/mainTwo/chargeManage">收费管理</router-link></el-menu-item>
          </el-submenu>
          <el-submenu index="4">
            <template slot="title"><i class="el-icon-menu"></i>重要信息管理</template>
            <el-menu-item index="4-1"> <router-link class="router" to="/mainTwo/editPage">修改资料</router-link></el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>

    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      width="30%">
      <span>是否退出</span>
      <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="exitSys">确 定</el-button>
  </span>
    </el-dialog>
  </el-container>
</template>

<script>
    export default {
        name: "mainTwo",
      data(){
          return{
            dialogVisible:false,
            userName: sessionStorage.getItem("userName")
          }
      },
    methods:{
      exitSys(){
        this.dialogVisible = false,
          this.$router.push("/");
      }
    }
    }
</script>

<style  scoped lang="scss">
  .el-header{
    background-color: #373d41;
    color: #DCDFE6;
    font-size: 20px;
    align-items: center;
    //text-align: center;
    line-height: 60px;
    display: flex;
    justify-content: space-between;
    > div {
      display: flex;
      align-items: center;
    }
  }

  .el-aside {
    background-color: #DCDFE6;
    color: #333;
    text-align: center;
    line-height: 120px;

  }

  .el-main {
    background-color: #E9EEF3;
    color: #333;
    //text-align: center;
    line-height: 42px;
    height: 100%;
  }

  .router{
    text-decoration: none;
  }
  /*body > .el-container {*/
  /*  margin-bottom: 40px;*/
  /*}*/

  .el-container{
    height: 800px;
  }


</style>



